import React from 'react'
import styled from 'styled-components';

const ToggleContainer = styled.button`
  background: ${({theme}) => theme.gradient};
  border: 1px solid ${({theme}) => theme.toggleBorder};
  border-radius: 45px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  position: fixed;
  margin: 5px 5px 5px 35px;
  padding: 2px;
  overflow: hidden;
  width: 50px;
  height: 25px;

  img {
    max-width: 2.5rem;
    height: auto;
    transition: all 0.3s linear;

    &:first-child {
      transform: ${({lightTheme}) => lightTheme ? 'translateY(0)' : 'translateY(100px)'};
    }

    &:nth-child(2) {
      transform: ${({lightTheme}) => lightTheme ? 'translateY(-100px)' : 'translateY(0)'};
    }
  }
`;

export const Toggle = ({theme, toggleTheme}) => {
    const isLight = theme === 'light';

    return (
        <ToggleContainer lightTheme={isLight} onClick={toggleTheme}>
            <img src="https://image.flaticon.com/icons/svg/1164/1164954.svg" width="24" height="24" alt="Sun free icon"
                 title="Sun free icon"/>
            <img src="https://image.flaticon.com/icons/svg/2033/2033921.svg" width="24" height="24" alt="Moon free icon"
                 title="Moon free icon"/>
        </ToggleContainer>
    );
};